    margin-right: auto;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
  @media (min-width: 1024px) {
    .cs-container {
      padding-left: 2.5rem;
      padding-right: 2.5rem;
    }
  }
  @media (min-width: 1280px) {
    .cs-container {
      padding-left: 5.625rem;
      padding-right: 5.625rem;
    }
  }
  .cs-container-wide {
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
  @media (min-width: 1024px) {
    .cs-container-wide {
      padding-left: 2.5rem;
      padding-right: 2.5rem;
    }
  }
  @media (min-width: 1280px) {
    .cs-container-wide {
      padding-left: 5.625rem;
      padding-right: 5.625rem;
    }
  }
  .bg-dot {
    background-image: radial-gradient(currentColor 1px, transparent 1px);
    background-size: 12px 12px;
    opacity: .06;
  }
  .cs-pill {
    display: inline-block;
    border: 1px solid #000;
    border-radius: 18.5px;
    padding: 8px 14px;
    font-size: 15px;
    line-height: 18px;
  }
  .cs-input {
    width: 100%;
    border-radius: 13px;
    background: #F3F3F3;
    padding: 12px 14px;
    font-size: 15px;
    line-height: 18px;
  }
  .cs-social {
    inline-size: 34px;
    block-size: 34px;
    border-radius: 9999px;
    border: 1px solid #fff;
    display: grid;
    place-items: center;
    font-size: 12px;
  }
  .i-play-white::after {
    content: "►";
    color: #fff;
    font-size: 28px;
    line-height: 1;
  }
  .i-caret-gray::after {
    content: "▾";
    color: #9A9A9A;
  }
  .i-arrow-black::after {
    content: "➜";
    color: #000;
  }
  .i-arrow-white::after {
    content: "➜";
    color: #fff;
  }
  .i-attach::after {
    content: "📎";
  }
}
